.flux-crosssell {
	&__product {
		margin-bottom: 20px;
		border: 1px solid $c-border;
		padding: 10px;
		border-radius: 4px;

		&-title {
			margin: 0;
			font-weight: 700;
			font-size: 16px;
			line-height: 1.5;
		}

		&-price {
			font-weight: 400;
			font-size: 14px;
			line-height: 1.5;
			
			span {
				font-weight: 400;
				font-size: 14px;
			}
		}
	}

	&__row {
		display: flex;
		justify-content: space-between;
		gap: 16px;

		.flux-crosssell__col--thumbnail {
			width: 80px;
			display: flex;
			align-self: center;
			flex-shrink: 0;

			img {
				width: 100%;
				height: auto;
			}
		}

		.flux-crosssell__col--title {
			flex-grow: 1;
			display: flex;
			flex-direction: column;
			justify-content: center;
		}

		.flux-crosssell__col--actions {
			width: 100px;
			flex-shrink: 0;
			display: flex;
			align-items: flex-start;
			justify-content: right;
		}
	}

	&__add-to-cart-btn {
		background: #fff;
		color: $c-text;
		border: 1px solid $c-text;
		border-radius: 4px;
		font-weight: 700;
		font-size: 14px;
		padding: 11px 16px;

		&:hover:not(:disabled) {
			background: $c-text;
			color: #fff;
			border-color: $c-text;
		}

		&:disabled {
			background: #AAAAAA;
			border: 1px solid #AAA;
			cursor: no-drop;
		}
	}

	&__description {
		margin-top: 12px;
		color: $c-text-subtle;
		font-size: 14px;
		line-height: 1.5;

		&:empty {
			display: none;
		}
	}

	&__variation-table {
		width: 100%;
		margin-top: 15px;

		tbody {
			width: 100%;
		}

		tr td {
			padding: 10px 6px;
			border-bottom: 0 !important;

			&:first-child {
				width: 120px;
			}
		}

		select {
			width: 250px;
		}

		.select2-selection__arrow {
			height: 42px !important;
		}
	}

	&__unavailable-msg {
		color: #fff !important;
	}

}

.woocommerce-checkout .flux-crosssell__product .select2-selection {
	height: 42px !important;
	padding: 7px 54px 9px 14px !important;
}